import styled from "styled-components";
import Header from "../../Components/Big/Header";
import Footer from "../../Components/Big/Footer";
import { motion } from "framer-motion";
import { useState } from "react";
import { Link } from "react-router-dom";

interface HoverProps {
  isHovered: boolean;
}

const Wrapper = styled.div`
  display: grid;
  grid-template-rows: 90px 1800px 330px;
  background-color: #1c1c1c;
  @media (max-width: 1100px) {
    grid-template-rows: 90px 1450px 330px;
  }
  @media (max-width: 880px) {
    grid-template-rows: 70px 1450px 330px;
  }
  @media (max-width: 940px) {
    grid-template-rows: 70px 1250px 250px;
  }
`;

const Banner = styled.div`
  background: radial-gradient(
    circle at 50% 600px,
    #303030,
    rgb(27, 27, 27) 37%
  );
  @media (max-width: 1100px) {
    background: radial-gradient(
      circle at 50% 550px,
      #303030,
      rgb(27, 27, 27) 37%
    );
  }
  @media (max-width: 940px) {
    background: radial-gradient(
      circle at 50% 500px,
      #303030,
      rgb(27, 27, 27) 37%
    );
  }
`;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Title = styled.div`
  margin-top: 180px;
  text-align: center;
  margin-bottom: 100px;
  p:nth-child(1) {
    font-size: 3.2vw;
    margin-bottom: 2.5vw;
    color: white;
  }
  p:nth-child(2) {
    font-size: 1.2vw;
    color: #d9d9d9;
  }
`;

const Projects = styled.div`
  display: flex;
  gap: 1.7vw;
  justify-content: center;
  margin-bottom: 20px;
`;

const ProjectBox = styled(motion.div)`
  width: 28vw;
  height: 28vw;
  background-color: #e1e1e1;
  border-radius: 20px;
  border: 1px solid #999999;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const ProjectText = styled.div<HoverProps>`
  width: 23vw;
  height: 23vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 1.5vw;
  color: white;

  p:nth-child(1) {
    color: ${(props) => (props.isHovered ? "#4e4e4e" : "#999999")};
    font-size: 1.1vw;
  }
  p:nth-child(2) {
    color: ${(props) => (props.isHovered ? "#2c2c2c" : "white")};
    font-size: 2.2vw;
  }
  p:nth-child(3) {
    color: ${(props) => (props.isHovered ? "#363636" : "#d9d9d9")};
    margin-top: 0.6vw;
    font-size: 1.1vw;
    line-height: 1.4vw;
  }
  p:nth-child(4) {
    color: ${(props) => (props.isHovered ? "#4e4e4e" : "#999999")};
    margin-top: 1.2vw;
    font-size: 1.1vw;
  }
`;

const ProjectHover = styled.div`
  position: relative;
  width: 23vw;
  height: 23vw;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  color: black;
  p:nth-child(n + 1) {
    line-height: 1.9vw;
    font-weight: 400;
    font-size: 1.1vw;
  }
`;

const ProjectLink = styled.div`
  position: absolute;
  right: 0px;
  bottom: 0px;
  span:nth-child(1) {
    color: #999999;
    font-weight: 400;
    font-size: 1.1vw;
    margin-right: 0.7vw;
  }
  img:nth-child(2) {
    width: 1.5vw;
  }
`;

const BoxHover = {
  start: {
    backgroundColor: "#1c1c1c",
  },
  hover: {
    backgroundColor: "#e1e1e1",
    transition: {
      backgroundColor: { duration: 0.5 },
    },
  },
};

function ProjectBoxComponent() {
  const [isHovered, setIsHovered] = useState(false);
  return (
    <ProjectBox
      variants={BoxHover}
      initial="start"
      whileHover="hover"
      onHoverStart={() => setIsHovered(true)}
      onHoverEnd={() => setIsHovered(false)}
    >
      {isHovered ? (
        <ProjectHover>
          <p>
            [  N     K  ]
                   tony9402 
            (https://github.com/tony9402/baekjoon) N 
                      
             .
          </p>
          <Link to="https://sup-algorithm.netlify.app/">
            <ProjectLink>
              <span> </span>
              <img src={`${process.env.PUBLIC_URL}/img/link.png`} />
            </ProjectLink>
          </Link>
        </ProjectHover>
      ) : (
        <ProjectText isHovered={isHovered}>
          <p>#Web</p>
          <p>SUP</p>
          <p>       </p>
          <p></p>
        </ProjectText>
      )}
    </ProjectBox>
  );
}

