function Project() {
  return (
    <Wrapper>
      <Header />
      <Banner>
        <Container>
          <Title>
            <p>Our Projects</p>
            <p>Release    .</p>
          </Title>
          <Projects>
            <ProjectBoxComponent />
            <ProjectBoxComponent1 />
            <ProjectBoxComponent2 />
          </Projects>
          <Projects>
            <ProjectBoxComponent3 />
            <ProjectBoxComponent4 />
            <ProjectBoxComponent5 />
          </Projects>
          <Projects>
            <ProjectBoxComponent6 />
            <ProjectBoxComponent7 />
          </Projects>
        </Container>
      </Banner>
      <Footer />
    </Wrapper>
  );
}

export default Project;
