import styled from "styled-components";
import SHeader from "../../Components/Small/SHeader";
import SFooter from "../../Components/Small/SFooter";
import { motion, useViewportScroll } from "framer-motion";
import { useEffect, useState } from "react";

const Wrapper = styled.div`
  display: grid;
  grid-template-rows: 70px 430px 450px 300px 450px 500px 550px 200px;
  @media (max-width: 450px) {
    grid-template-rows: 70px 350px 370px 270px 350px 400px 450px 180px;
  }
  background: radial-gradient(
    circle at 50% 300px,
    #303030,
    rgb(27, 27, 27) 60%
  );
  position: relative;
`;

const Back = styled.div`
  display: flex;
  flex-direction: column;
`;

const Blank = styled.div``;

const Banner = styled.div`
  background-image: url(${process.env.PUBLIC_URL}/img/bg1.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  width: 100%;
  position: absolute;
  top: 0;
  height: 500px;
`;

const BannerText = styled(motion.div)`
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  margin-bottom: 10vw;
  p:nth-child(1) {
    color: white;
    font-size: 5vw;
  }
`;

const Mission = styled.div`
  height: 450px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;
  p:nth-child(1) {
    font-size: 5.7vw;
    margin-bottom: 5vw;
  }
  p:nth-child(n + 2) {
    font-weight: 400;
    color: #999999;
    font-size: 2.8vw;
    margin-bottom: 1.5vw;
  }
`;

const ImageZone = styled(motion.div)`
  display: grid;
  grid-template-columns: 1fr 15fr;
`;

const Image1 = styled(motion.div)`
  background-image: url(${process.env.PUBLIC_URL}/img/bg2.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
`;

const Projects = styled(motion.div)`
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
`;

const ProjectsText = styled(motion.div)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  p:nth-child(1) {
    font-size: 5.7vw;
    color: white;
    margin-bottom: 5vw;
    span {
      color: #999999;
    }
  }
  p:nth-child(n + 2) {
    font-size: 2.8vw;
    color: #999999;
    margin-bottom: 1.5vw;
    font-weight: 400;
  }
`;

const Button = styled.button`
  margin-top: 30px;
  width: 25vw;
  height: 6vw;
  background: transparent;
  border: 1px solid #f6c015;
  border-radius: 7vw;
  display: flex;
  justify-content: center;
  align-items: center;
  p:nth-child(1) {
    margin: 0;
    font-size: 1.9vw;
    font-weight: 400;
    color: #b6b6b6;
  }
`;

const Image2 = styled(motion.div)`
  background-image: url(${process.env.PUBLIC_URL}/img/bg3.jpg);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  width: 100%;
`;

const Text2 = styled(motion.div)`
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  margin-bottom: 10vw;
  p:nth-child(1) {
    font-size: 5vw;
    color: white;
    margin-bottom: 1.5vw;
    span {
      color: #999999;
    }
  }
  p:nth-child(2) {
    font-size: 5vw;
    color: white;
    margin-bottom: 5vw;
    span {
      color: #999999;
    }
  }
  p:nth-child(n + 3) {
    font-size: 2.5vw;
    font-weight: 400;
    color: #999999;
    margin-bottom: 1vw;
  }
`;

const Seminars = styled.div`
  display: grid;
  grid-template-columns: 1fr 5fr 1fr;
  gap: 1.5vw;
`;

const Seminar = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
`;

const SeminarText = styled(motion.div)`
  width: 45vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  p: nth-child(1) {
    font-size: 4vw;
    color: white;
    margin-bottom: 5vw;
  }
  p: nth-child(2) {
    font-size: 1.7vw;
    font-weight: 400;
    color: #999999;
    margin-bottom: 5vw;
  }
`;

const SeminarContent = styled(motion.div)`
  width: 100%;
  height: 55px;
  border-radius: 8px;
  background-color: #292929;
  position: relative;
  margin-bottom: 15px;
  p: nth-child(1) {
    position: absolute;
    top: 15px;
    left: 10px;
    font-size: 1.9vw;
    color: #d9d9d9;
    margin-bottom: 0px;
    margin-left: 20px;
  }
  p: nth-child(2) {
    position: absolute;
    bottom: 15px;
    right: 10px;
    font-size: 1.3vw;
    color: #999999;
    margin-bottom: 0px;
    margin-right: 20px;
  }
`;

function SmallAbout() {
  const { scrollYProgress } = useViewportScroll();
  const [isVisible1, setIsVisible1] = useState(false);
  const [isVisible2, setIsVisible2] = useState(false);
  const [isVisible3, setIsVisible3] = useState(false);
  useEffect(() => {
    const unsubscribe = scrollYProgress.onChange((latest) => {
      if (latest >= 0.3) {
        setIsVisible1(true);
      }
      if (latest >= 0.6) {
        setIsVisible2(true);
      }
      if (latest >= 0.8) {
        setIsVisible3(true);
      }
    });
    return () => unsubscribe();
  }, [scrollYProgress]);

  return (
    <Wrapper>
      <SHeader />
      <Back>
        <Banner>
          <Blank />
          <BannerText
            initial={{ opacity: 0, scale: 1, y: 15 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            transition={{ duration: 0.4, delay: 0.5 }}
          >
            <p>  </p>
          </BannerText>
          <Blank />
        </Banner>
      </Back>
      <Mission>
        <p>Mission</p>
        <p>Release 2014 </p>
        <p>    ,</p>
        <p>  </p>
        <p>     .</p>
        <p>       ,</p>
        <p>    .</p>
      </Mission>
      <ImageZone>
        <div></div>
        {isVisible1 && (
          <Image1
            initial={{ opacity: 0, scale: 1, y: 15 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            transition={{ duration: 0.4 }}
          />
        )}
      </ImageZone>
      <Projects>
        <Blank />
        {isVisible1 && (
          <ProjectsText
            initial={{ opacity: 0, scale: 1, y: 15 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            transition={{ duration: 0.4 }}
          >
            <p>
               <span> .</span>
            </p>
            <p>Release  ,  ,   </p>
            <p>   .</p>
            <p>     ,</p>
            <p>      .</p>
            <p>    ,     .</p>
            <Button>
              <p>  </p>
            </Button>
          </ProjectsText>
        )}
        <Blank />
      </Projects>
      {isVisible2 ? (
        <Image2
          initial={{ opacity: 0, scale: 1, y: 15 }}
          animate={{ opacity: 1, scale: 1, y: 0 }}
          transition={{ duration: 0.4 }}
        >
          <Blank />
          <Text2
            initial={{ opacity: 0, scale: 1, y: 15 }}
            animate={{ opacity: 1, scale: 1, y: 0 }}
            transition={{ duration: 0.4, delay: 0.7 }}
          >
            <p>
                <span> </span>
            </p>
            <p>
                 
              <span> .</span>
            </p>
            <p>
              Release   ,   
            </p>
            <p>
                       .
            </p>
            <p>
                     ,
            </p>
            <p>     .</p>
          </Text2>
          <Blank />
        </Image2>
      ) : (
        <Blank />
      )}
      <Seminars>
        <Blank />
        {isVisible3 ? (
          <Seminar>
            <SeminarText
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4 }}
            >
              <p>   </p>
              <p> , Release  00  .</p>
            </SeminarText>
            <SeminarContent
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4, delay: 0.7 }}
            >
              <p>AI   :   </p>
              <p> </p>
            </SeminarContent>
            <SeminarContent
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4, delay: 0.7 }}
            >
              <p>   </p>
              <p> </p>
            </SeminarContent>
            <SeminarContent
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4, delay: 0.7 }}
            >
              <p>   </p>
              <p> </p>
            </SeminarContent>
            <SeminarContent
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4, delay: 0.7 }}
            >
              <p> </p>
              <p> </p>
            </SeminarContent>
            <SeminarContent
              initial={{ opacity: 0, scale: 1, y: 15 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              transition={{ duration: 0.4, delay: 0.7 }}
            >
              <p>   </p>
              <p> </p>
            </SeminarContent>
          </Seminar>
        ) : (
          <Blank />
        )}
        <Blank />
      </Seminars>
      <SFooter />
    </Wrapper>
  );
}

export default SmallAbout;
