import styled from "styled-components";

const Wrapper = styled.div`
  display: grid;
`;

const First = styled.div`
  padding-left: 80px;
  padding-top: 300px;
`;

const Text = styled.div`
  display: flex;
  flex-direction: column;
  p:nth-child(1) {
    color: #eeeeee;
    font-size: 2.4vw;
    margin-bottom: 36px;
  }
  p:nth-child(n + 2) {
    color: #999999;
    font-size: 1.1vw;
    margin-bottom: 8px;
  }
`;

function Activity() {
  return (
    <Wrapper>
      <First>
        <Text>
          <p>Release  </p>
          <p>
            Release , , ,     
            
          </p>
          <p>   .</p>
        </Text>
      </First>
    </Wrapper>
  );
}

export default Activity;
